<template>
  <div class="m-header">
    <mt-header title="Chicken Music">
	  <mt-button class='fa fa-user' slot="right"></mt-button>
	</mt-header>
  </div>
</template>

<script>
	import {Header,Button} from 'mint-ui'
	import 'mint-ui/lib/style.css'
	export default {
	  components:{
	  	'mt-header':Header,
	  	'mt-button':Button
	  }
	}
</script>

<style scoped lang="stylus" rel='stylesheet/stylus'>
@import "~assets/stylus/base";
.mint-header{
	background:$bg-color;
	color:#FFCD32;
	height: 44px;
	font-size: 18px
}
.fa-user:before{
	width: 16px;display: inline-block;
}
</style>



