<!--创建scroll组件-->
<template>
  <div class="loading">
   		<img src="./loading.gif"/>
			<p>{{title}}</p>
  </div>
</template>

<script>
	export default {
		//接收参数
			props:{
				title:{
					type:String,
					default:'拼命加载中...'
				}
			}
	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
.loading{
	width: 100%;
	text-align: center;
}
.loading img{
	width: 24px;
	height: 24px;
}
.loading p{
	line-height: 15px;
	font-size: 15px;
	margin-top: 10px;
	color: #fff;
}
</style>



