//封装一个类
export function songsClss(id,mid,singer,name,album,duration,img,urls){
	this.id=id
	this.mid=mid
	this.singer=singer
	this.name=name
	this.album=album
	this.duration=duration
	this.img=img
	this.urls=urls
}

export function createNewSongsList(data){
	return new songsClss(
		data.songid,
		data.songmid,
		filterSinger(data.singer),
		data.songname,
		data.albumname,
		data.interval,
		"https://y.gtimg.cn/music/photo_new/T002R300x300M000"+data.albummid+".jpg",
//		`http://dl.stream.qqmusic.qq.com/C400${data.strMediaMid}.m4a?vkey=56E3865FCAA5367A2AEA4419CB3007162BFB3BEC4E94DF3186DB0376330FFEE20DC8E244D4A754FFF666A86C4035447E627A764DB76F874E&guid=9682305044&uin=0&fromtag=66`
		`http://isure.stream.qqmusic.qq.com/C100${data.songmid}.m4a?&fromtag=32`
//		http://isure.stream.qqmusic.qq.com/C100004cNNNW0QG6RR.m4a?fromtag=32
//		`http://ws.stream.qqmusic.qq.com/${data.songid}.m4a?fromtag=46`
//		https://y.qq.com/n/yqq/album/003bSL0v4bpKAx.html
//http://dl.stream.qqmusic.qq.com/C400001J5QJL1pRQYB.m4a?vkey=56E3865FCAA5367A2AEA4419CB3007162BFB3BEC4E94DF3186DB0376330FFEE20DC8E244D4A754FFF666A86C4035447E627A764DB76F874E&guid=9682305044&uin=0&fromtag=66

//http://dl.stream.qqmusic.qq.com/C400001J5QJL1pRQYB.m4a?vkey=56E3865FCAA5367A2AEA4419CB3007162BFB3BEC4E94DF3186DB0376330FFEE20DC8E244D4A754FFF666A86C4035447E627A764DB76F874E&guid=9682305044&uin=0&fromtag=66
//http://dl.stream.qqmusic.qq.com/C400003OUlho2HcRHC.m4a?vkey=1A5D0DDBB25FE8BD9474E2987499D7414D242FFBC6FD3768A0F33E1CA573BDECBE4B2FD5F5DC67F1CA678A08135B7C33534D080D213A7D06&guid=9682305044&uin=0&fromtag=66

	)
}

//过滤歌手数据
function filterSinger(singerData){
	let arr=[];
	if(!singerData){
		return ''
	}
	singerData.forEach(function(singer){
		arr.push(singer.name)
	})
	//用"/"连接成字符串
	return arr.join('/')
}








