<template>
	<div class="singe-detail">
			<div class="back" @click="back">
				<i class="fa fa-chevron-left" aria-hidden="true"></i>
			</div>
			<h2 class="singer-name" v-html="title"></h2>
			<div class="singer-bg" :style="bgStyle" ref='singerBg'>
				<div class="suijiplays">
					<div class="suijiplays-1">
						<i class="fa fa-play-circle-o"></i>
						<span style="font-size: 12px;">随机播放全部</span>
					</div>

				</div>
				<div class="filters"></div>
			</div>
			<div class="bg-layer" ref="bgLayer"></div>
			<Scroll @scrollFun="scrolls" class='Scroll' ref='ScrollList' :probe-type='probeType' :listen-scroll="listenScroll">
																							<!--接收基础组件songlist的派发的方法-->
				<SongList  :songsdata="singerdetail" :title="title" class="song-list-wrap" @select='selectItem'></SongList>
				<div class="loading-wrap" v-show="!this.singerdetail">
					<Loading></Loading>
				</div>
			</Scroll>
		</div>
</template>
<script>
	import Scroll from 'base/scroll/scroll.vue'
	import SongList from 'base/song-list/SongList.vue'
	import Loading from 'base/loading/loading.vue'
	import {mapActions} from 'vuex'
	export default {
		components:{
			Scroll,
			SongList,
			Loading
		},
		props:{
			title:{
				type:String,
				default:''
			},
			bgImg:{
				type:String,
				default:''
			},
			singerdetail:{
				type:Array,
				default:[]
			}
		},
		data(){
			return {
				scrollY:0
			}
		},
		mounted(){
			this.clientH=this.$refs.singerBg.clientHeight
			this.minH=-this.clientH+40
			this.$refs.ScrollList.$el.style.top=this.$refs.singerBg.clientHeight+'px'

		},
		created(){
			this.probeType = 3
      		this.listenScroll = true
		},
		computed:{
			bgStyle(){
				return 'background-image:url("https://y.gtimg.cn/music/photo_new/T001R300x300M000'+this.bgImg+'.jpg")'
			}
		},
		methods:{
			scrolls(pos){
				this.scrollY=pos.y
			},
			back(){
				return this.$router.back()
			},
			//基础组件派发过来的方法
			selectItem(item,index){
				console.log(item)
				console.log(index)
				this.selectPlay({
					list:this.singerdetail,
					index
				})
			},
			...mapActions([
				//注册这个方法，才能用
				'selectPlay'
			])
		},
		watch:{
			singerdetail(){
				setTimeout(()=>{
						this.$refs.ScrollList.refresh()
					},20)
			},
			scrollY(newY){
				let lastH=Math.max(this.minH,newY)
				let zindex=0
				let scale=1;
				const bili=Math.abs(newY/this.minH)
				if(newY >0){
					scale=1+bili
					zindex=10
				}

				this.$refs.singerBg.style['transform']='scale('+scale+')'
				this.$refs.bgLayer.style['transform']='translate3d(0,'+lastH+'px,0)'
				if(newY < this.minH){
					zindex=10
					this.$refs.singerBg.style.paddingTop=0;
					this.$refs.singerBg.style.height='40px'
				}else{
					this.$refs.singerBg.style.paddingTop='70%';
					this.$refs.singerBg.style.height=0
				}
				this.$refs.singerBg.style.zIndex=zindex
			}
		}
	}
</script>
<style rel='stylesheet/stylus' lang="stylus" scoped>
.singe-detail{
	position: fixed;
	z-index:100;
	top: 0;left: 0;bottom: 0;right: 0;
	background: #222;
}
.slider-enter-active,.slider-leave-active{
	 transition: all 1s;
}
.slider-enter,.slider-leave-to{
	transform: translateX(100%);
}
.back{
	position: absolute;
    left: 6px;
    top: 0;
    color:#FFCD32;
    height: 40px;
    width: 40px;
    line-height: 52px;
    font-size: 26px;
    z-index: 50;
}
.singer-name{
	width: 70%;height: 40px;position: absolute;left: 15%;font-size: 18px;line-height: 40px;font-weight: 500;z-index: 40;color:#fff
}
.singer-bg{
	position: relative;
	width: 100%;
	height: 0;
	padding-top: 70%;
	/*background-image: url(./xzq.jpg);*/
	background-size: cover;
	background-repeat: no-repeat;
	background-position: left top;
}
.suijiplays{
	position: absolute;
	bottom: 20px;
	width: 100%;
	z-index: 50;
}
.suijiplays-1{
	position: absolute;bottom: 20px;
	width: 130px;
	left: 50%;margin-left: -65px;
	border: 1px solid #FFCD32;border-radius: 20px;
	line-height: 32px;
	height: 32px;
	color:#FFCD32 ;
	z-index: 50;
}
.suijiplays-1 i{
	vertical-align: middle;
}
.filters{position: absolute;top: 0;left: 0;height:100%;width:100%;background: rgba(0,0,0,.5);}
.bg-layer{
	position: relative;height: 100%;background:#222;
}
.Scroll{
	position: fixed;
	top: 0;left:0;right:0;bottom: 0;background: rgb(34, 34, 34);
}
.loading-wrap{width: 100%;position: absolute;top: 50%;}
</style>


