<template>
  <div class="searchBox">
			<div class="searchBox-content">
					<i class="fa fa-search" aria-hidden="true"></i>
					<input type="text" class="inputSearch" :placeholder="placeHolder" v-model="searchText"/>
					<i class="fa fa-times" aria-hidden="true" v-show="searchText" @click="clear"></i>
			</div>
  </div>
</template>

<script>
	export default {
				props:{
					placeHolder:{
						type:String,
						default:''
					}
				},
				data(){
					return {
						searchText:''
					}
				},
				methods:{
					clear(){
						this.searchText=''
					},
					setQuery(k){
						this.searchText=k
					}
				},
				watch:{
					searchText(newText){
						this.$emit('searchs',this.searchText)
					}
				}
	}
</script>

<style scoped lang="stylus" rel='stylesheet/stylus'>
@import '~assets/stylus/base.styl';
	.searchBox{
		padding:20px 15px;
		color: #fff;
	}
	.searchBox-content{
		display: flex;
		align-items: center;
		background: #333333;
		border-radius: 5px;
	}
	.inputSearch{
		height: 20px;margin: 0;
		margin-right: 10px ;
		background: none;
		border:none;
		line-height: 20px;
		font-size: 13px;
		padding: 0;
		border-radius:5px;
	}
	.inputSearch:focus{
		border: 1px solid #8C651A;
	}
	.searchBox-content .fa-search{
		padding: 15px 10px;
	}
	.searchBox-content .fa-times{
		padding: 10px 10px 10px 0;
	}
</style>



