import Vue from 'vue'
import Vuex from 'vuex'
import state from './state.js'
//里面多个方法，用  " * as "
import * as actions from './actions.js'
import * as getters from './getters.js'
import mutations from './mutations.js'

Vue.use(Vuex);
export default new Vuex.Store({
	state,
	getters,
	mutations,
	actions
})




