import json2 from 'api/json.js'

export function getRank(){
	var url="https://c.y.qq.com/v8/fcg-bin/fcg_myqq_toplist.fcg";
	var data=[
		{g_tk:5381},
		{uin:0},
		{format:'jsonp'},
		{inCharset:'utf-8'},
		{outCharset:'utf-8'},
		{notice:0},
		{platform:'h5'},
		{needNewCode:1},
		{_:1517472364884}
	];
	return json2(url,data);
}



