import {setSearch,deleatOne,deleatAll} from 'api/catch.js'
//第一个参数是对象，（提交的方法名字，状态），第二个是{参数对象，参数索引}
export const selectPlay = function({commit,state},{list,index}){
	//用commit(方法,值)
	//执行setShunxuList方法，职位list
	commit('setShunxuList',list)
	commit('setPlayList',list)
	commit('setCurrentIndex',index)
	commit('setFullScreen',true)
}

//封装一个操作多个vuex的方法，
export const insertSong =function({commit,state},song){
	//得到几个要改变的状态
	let playList =state.playList
	let shunxuList =state.shunxuList
	let currentIndex=state.currentIndex

	currentIndex++
	playList.splice(currentIndex,0,song)
	//记录当前歌曲
	let currentSong=playList[currentIndex]

	//提交commit方法
	commit('setPlayList',playList)
	commit('setShunxuList',shunxuList)
	commit('setCurrentIndex',currentIndex)
	commit('setFullScreen',true)
}

//保存搜索结果
export const saveSearch= function({commit},query){
	commit('setSearchHistory',setSearch(query))
}

//删除一条记录
export const clearOne =function({commit},text){
	commit('setSearchHistory',deleatOne(text))
}

//删除全部记录
export const clearAll =function({commit}){
	commit('setSearchHistory',deleatAll())
}



