<template>
	<transition name='slider'>
		<MusicList :title="title" :bgImg="bgImg" :singerdetail='singerdetail'></MusicList>
	</transition>
</template>
<script>
	import 'mint-ui/lib/style.css'
	//取mutations里面的方法，并且调用getters里面的方法，先饮用
	import {mapGetters} from 'vuex'
	import {getSingerList} from 'api/singer.js'
	import {getvkey} from 'api/singer.js'
	import MusicList from 'base/music-list/musicList.vue'
	import {createNewSongsList} from 'assets/js/songlist.js'
	export default{
		data(){
			return {
				singerdetail:[]
			}
		},
		components:{
			MusicList
		},
		computed:{
			...mapGetters([
				//getter.js里面的mapSingerFun方法
				//g数m对 （getter,actions数组，mutations对象）
				'mapSingerFun'
			]),
			title(){
				return this.mapSingerFun.Fsinger_name
			},
			bgImg(){
				return this.mapSingerFun.Fsinger_mid
			}
		},
		created(){
			this._getLists(this.mapSingerFun.Fsinger_mid,50)
			console.log(this.mapSingerFun.Fsinger_name)
		},
		methods:{
			_getLists(mid,num){
				console.log(mid)
				if(!mid){
					this.$router.push({path:'/singer'})
					return
				}
				//获取歌手详情数据列表
				getSingerList(mid,num).then((msg)=>{
					this.singerdetail=this._createNewData(msg.data.list)
					console.log(msg.data.list)
				})
			},
			//把综合数据，处理成想要的格式数据
			_createNewData(datas){
				let arr=[]
				datas.forEach(function(data){
					if(data.musicData.songid){
//						把循环的数据用createNewSongsList（）处理之后，再放入arr中
						arr.push(createNewSongsList(data.musicData))
					}
				})
				console.log(arr)
				return arr
			}
		}
	}


</script>
<style rel="stylesheet/stylus" lang="stylus" scoped="scoped">
.singe-detail{
	position: fixed;
	z-index:998;
	top: 0;left: 0;bottom: 0;right: 0;
	background: #222;
}
.slider-enter-active,.slider-leave-active{
	 transition: all 1s;
}
.slider-enter,.slider-leave-to{
	transform: translateX(100%);
}
.back{
	position: absolute;
    left: 6px;
    top: 0;
    color:#FFCD32;
    height: 40px;
    width: 40px;
    line-height: 52px;
    font-size: 26px;
}
.singer-name{
	width: 70%;height: 40px;position: absolute;left: 15%;font-size: 18px;line-height: 40px;font-weight: 500;
}
.singer-bg{
	position: relative;
	width: 100%;
	height: 0;
	padding-top: 70%;
	/*background-image: url(./xzq.jpg);*/
	background-size: cover;
	background-repeat: no-repeat;
	background-position: left top;
	z-index: -1;
}
.suijiplay{
	position: absolute;bottom: 20px;
	width: 130px;
	left: 50%;margin-left: -65px;
	border: 1px solid #FFCD32;border-radius: 20px;
	line-height: 32px;
	height: 32px;
	color:#FFCD32 ;
	z-index: 50;
}.suijiplay i{
	vertical-align: middle;
}
.filter{position: fixed;top: 0;left: 0;right: 0;bottom: 0;background: rgba(0,0,0,.5);}
</style>


