<template>
	<div class="no-result">
		<div class="no-result-icon"></div>
    	<p class="no-result-text">{{title}}</p>
	</div>
</template>
<script>
	export default{
		props:{
			title:{
				type:String,
				default:''
			}
		}
	}
</script>
<style rel="stylesheet/stylus" lang="stylus" scoped="scoped">
	.no-result{
		 text-align: center;
	}

    .no-result-icon{
    	width: 86px;
	    height: 90px;
	    margin: 0 auto;
	    background: url('./no-result@3x.png');
	    background-size: 86px 90px
    }

    .no-result-text{
    	margin-top: 30px;
	    font-size: 14px;
	    color:rgba(255,255,255,.3)
    }

</style>


