//缓存搜索的数据
export function setSearch(obj){
	//历史记录的localstorage的键值
	var k='his'
	//取出历史记录数组
	let oldSearch=getSearch(k)
	//如果第一次没记录，直接返回取出的local数组
	if(typeof obj =='undefined'){
		return oldSearch
	}
	//如果传来有值，判断改值是否在缓存数组中，过滤，再返回缓存数组
	if(oldSearch){
		for(let i=0;i<oldSearch.length;i++){
			if(obj === oldSearch[i]){
				return oldSearch
			}
		}
	}
	//添加到数组的第一位
	oldSearch.unshift(obj)
	//历史记录长度大于10，就删除最后一个
	if(oldSearch.length>10){
		oldSearch.pop()
	}
	//如果传来的有值，存到缓存中
	if(obj){
		localStorage.setItem('his',JSON.stringify(oldSearch))
		return oldSearch
	}

	return oldSearch
}

//删除搜索记录的一条数据
export function deleatOne(text){
	//历史记录的localstorage的键值
	var k='his'
	//取出历史记录数组
	let searchData=getSearch(k)
	let index=searchData.indexOf(text)
	if(index >-1){
		searchData.splice(index,1);
		localStorage.setItem('his',JSON.stringify(searchData))
	}
	return searchData
}

//删除全部记录
export function deleatAll(){
	localStorage.removeItem('his')
	return []
}

export const getSearch = function(k){
	//取出localstorage,并解码json字符串为json对象
	return localStorage.getItem(k)? JSON.parse(localStorage.getItem(k)) : []
}



