<template>
  <div class="play" v-show="mapPlayList.length">
  	<transition name='fullscreen'>
		  	<div class="fullscreen" v-show="mapFullScreen">
			  		<div class="blurBg">
			  			<img width="100%" height="100%" :src="mapCurrentSong.img"/>
			  		</div>
			  		<div class="top">
			  			<div class="back" @click="back">
									<i class="fa fa-chevron-down" aria-hidden="true"></i>
							</div>
							<h2 class="singer-name" v-html="mapCurrentSong.name"></h2>
							<h3 class="singerName" v-html="mapCurrentSong.singer"></h3>
			  		</div>
			  		<div class="content">
			  				<div class="content-l" style="display: none1;">
			  						<div class="song-img round" :class="animateType">
												<div class="song-img-1">
													<img width="100%" height="100%" :src="mapCurrentSong.img"/>
												</div>
			  						</div>
			  						<div class="song-desc">我是歌词</div>
			  				</div>
			  				<div class="content-r" style="display: none;">
										<div class="song-lycing">
												<p>还得对方地方的对方地方对方地方对方地方对方地方</p>
												<p>还得对方地方的对方地方对方地方对方地方对方地方</p>
										</div>
			  				</div>
			  		</div>
            <div class="bottom">
            	<div class="dot-wraper">
            		<span class="active"></span>
            		<span></span>
            	</div>
            	<div class="jdt">
            		<span class="time-l time">{{format(currentTime)}}</span>
            		<div class="jdt-1">
            			 <div class="jdt-warper">
            			 		<div class="jdt-warper-sheng">
													<div class="jdt-already" ref='jdtAlready'></div>
													<div class="jdt-btn" ref='jdtBtn'>
															<div></div>
													</div>
            			 		</div>
            			 </div>
            		</div>
            		<span class="time-r time">{{format(mapCurrentSong.duration)}}</span>
            	</div>
            	<ul class="play-list">
            		<li @click="setPlayMode">
            			<i class="fa" :class="modeIcon"></i>
            		</li>
            		<li @click.stop="preSong" :class="disableCle">
            			<i class="fa fa-fast-backward"></i>
            		</li>
            		<li @click.stop="setPlay" :class="disableCle">
            			<i class="fa" :class="playBtn"></i>
            		</li>
            		<li @click.stop="nextSong" :class="disableCle">
            			<i class="fa fa-fast-forward"></i>
            		</li>
            		<li @click.stop="collection">
            			<i class="fa fa-heart"></i>
            		</li>
            	</ul>
            </div>
		  	</div>
	  </transition>
	  <transition name='mini'>
		  	<div class="miniscreen" v-show="!mapFullScreen">
		  		<ul class="miniUl" @click="openFullscreen">
							<li class="mini-img round" :class="animateType">
								<img width="100%" height="100%" :src="mapCurrentSong.img" />
							</li>
							<li class="mini-text">
								<p v-html="mapCurrentSong.name"></p>
								<p v-html="mapCurrentSong.singer"></p>
							</li>
							<li class="mini-play"  cla="mini-play" @click.stop="setPlay">
								<i class="fa" :class="playBtn"></i>
							</li>
							<li class="mini-list">
								<i class="fa fa-bars"></i>
							</li>
		  		</ul>
		  	</div>
		</transition>
		<!--audio音乐播放器-->
		<audio ref='audio' :src="mapCurrentSong.urls" @canplay="canplay" @error="errorplay" @timeupdate="timeUpdate"></audio>
  </div>
</template>

<script>
//	获取vuex的内容
	import {mapGetters,mapMutations} from 'vuex'
	export default{
		data(){
			return {
				//连续点击
				canPlayType:false,
				//当前播放时间
				currentTime:0
			}
		},
		computed:{
			...mapGetters([
				'mapFullScreen',
				'mapPlayList',
				'mapShunxuList',
				'mapPlaying',
				'mapCurrentSong',
				'mapMode',
				'mapCurrentIndex'
			]),
//			切换播放按钮
			playBtn(){
				return this.mapPlaying ? 'fa-play-circle-o' : 'fa-pause-circle'
			},
//			切换旋转
			animateType(){
				return this.mapPlaying ? 'paused' : 'running'
			},
			//禁止点击快了的状态
			disableCle(){
				return this.canPlayType ? '' :'disable'
			},
			//播放模式
			modeIcon(){
				console.log(this.mapMode)
				console.log(this.mapCurrentIndex)
				console.log(this.mapMode)
				return this.mapMode == '0' ? 'fa-exchange' : this.mapMode == '1' ? 'fa-random' : 'fa-italic'
				console.log(this.mapMode)
			}
		},
		methods:{
			...mapMutations({
				setFullScreen:'setFullScreen',
				'setPlaying':'setPlaying',
				'setCurrentIndex':'setCurrentIndex',
				'setMode':'setMode',
				'setPlayList':'setPlayList'
			}),
			back(){
				this.setFullScreen(false)
			},
			openFullscreen(){
				this.setFullScreen(true)
			},
			setPlay(){
				this.setPlaying(!this.mapPlaying)
			},
			setPlayMode(){
				//新数组
				let newList=null
				const newMode =(this.mapMode + 1) % 3
				this.setMode(newMode)
				if(newMode === 1){
						//随机，先把列表的数据保存，再打乱放入
						newList = this.mapPlayList
						newList.sort(function(){
							return Math.random() -.5
						})
						console.log('随机')
						console.log(newList)
						//修改索引获取当前歌曲在新数组中的索引
						this.updateIndex(newList)
				}else{
						//如果点了顺序，把顺序列表的值保存在新的数组里面，后面一起改
						newList=this.mapShunxuList
				}

				//最后一起改列表的顺序
				this.setPlayList(newList)
			},
			//修改索引获取当前歌曲index
			updateIndex(list){
					let newIndex=list.indexOf(this.mapCurrentSong)
					this.setCurrentIndex(newIndex)
			},
			preSong(){
				console.log('上一首');
				//如果为false，表示点击快了，并获取数据失败
				if(!this.canPlayType){
					return
				}
				let index=this.mapCurrentIndex-1
				if(index<0){
					index=this.mapPlayList.length-1
				}
				this.setCurrentIndex(index)
				//重置播放状态
				if(this.mapPlaying){
					this.setPlay()
				}
				//重置为false
				this.canPlayType=false
			},
			nextSong(){
				console.log('下一首')
				//如果为false，表示点击快了，并获取数据失败
				if(!this.canPlayType){
					return
				}
				let index=this.mapCurrentIndex+1
				if(index ==this.mapPlayList.length){
					index=0
				}
				this.setCurrentIndex(index)
				//重置播放状态
				if(this.mapPlaying){
					this.setPlay()
				}
//				重置为false
				this.canPlayType=false
			},
			collection(){
				console.log('收藏')
				let currentIndex=this.mapCurrentIndex
				console.log(currentIndex)
//				this.setCurrentIndex(1)
			},
			canplay(){
				this.canPlayType=true
			},
			errorplay(){
				this.canPlayType=true
			},
			timeUpdate(e){
				this.currentTime=e.target.currentTime
			},
//			时间转换
			format(times){
				let time=Math.floor(times);
				let miute=Math.floor(time/60)
				let second= time%60
				if(second<10){
					second='0'+second
				}
				return miute+':'+second
			}
		},
		watch:{
			mapCurrentSong(){
				this.$nextTick(()=>{
					setTimeout(()=>{
						this.$refs.audio.play()
					},300)

				})
				console.log(this.mapCurrentSong)
				console.log(this.mapPlaying)

			},
			mapPlaying(newPlaying){
				let audioObj=this.$refs.audio
				this.$nextTick(()=>{
						newPlaying ? audioObj.pause() :audioObj.play()
				})
			},
			currentTime(newT){
				if(newT >0){
					let w=newT/this.mapCurrentSong.duration*100
					if(w>100){
						w=100
						this.setPlaying(false)
					}
					this.$refs.jdtAlready.style.width=w+'%'
					this.$refs.jdtBtn.style.left=w+'%'
				}
			}
		}
	}
</script>

<style scoped lang="stylus" rel='stylesheet/stylus'>
h2,h3{margin: 0;padding: 0;}
.play{color: #fff}
.fullscreen{
	position: fixed;
	top: 0;left: 0;right: 0;bottom: 0;
	z-index: 150;
	background: #222;
}
.jdt{
	display: flex;
	width: 80%;
	margin: 0 auto;
	padding: 10px 0;
}
.jdt .jdt-1{
	flex: 1;
}
.running{
	animation-play-state:running!important
}
.paused{
	animation-play-state:paused!important
}
.round{
	animation: zhuan 20s linear infinite;
}
@keyframes zhuan{
	from{transform: rotate(00deg);}
	to{transform: rotate(360deg);}
}
.miniUl{display: flex;height:60px;align-items:center;}
.miniUl li{height: 42px;align-items: center;line-height: 42px;}
.miniUl li.mini-img{flex:0 0 40px;width: 40px;height: 40px;border-radius: 50%;overflow: hidden;margin:0 10px 0 20px}
.miniUl li.mini-text{flex:1;font-size: 12px;line-height: 14px;color:rgba(255,255,255,0.3);text-align: left;}
.miniUl li.mini-text>p:first-child{font-size: 14px;color:#fff}
.miniUl li.mini-play{flex:0 0 30px;padding: 0 10px;font-size: 30px;color: #ffcd32}
.miniUl li.mini-list{flex:0 0 30px;padding: 0 10px;font-size: 30px;color: #ffcd32}
.jdt .time{
	flex: 0 0 30px;
	font-size: 12px;
}
.jdt-warper{height: 30px;padding: 0 5px;}
.jdt-warper-sheng{position:relative;height: 4px;top:4px;background:rgba(0,0,0,0.3);}
.jdt-warper-sheng .jdt-already{position:absolute;height: 4px;top:0px;background: #FFCD32;width:0%;}
.jdt-btn{position: absolute;width: 16px;height: 16px;left:0%;border:4px solid #fff;top: -6px;margin-left: -8px;border-radius: 50%;}
.jdt-btn>div{position: absolute;width: 8px;height: 8px;left:0;top:0;background:#FFCD32;border-radius: 50%;}
.play-list{
	display: flex;
}
.play-list li{
	flex:1;
	color: #ffcd32;
	font-size: 25px;
	display: inline-block;
}
.play-list li.disable{
	color:rgba(255,205,50,.4);
}
.dot-wraper{
	text-align: center;
}
.dot-wraper span{
	  display: inline-block;
    vertical-align: middle;
    margin: 0 4px;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: rgba(255,255,255,0.5);
}
.dot-wraper span.active{
	  width: 20px;
    border-radius: 5px;
    background: rgba(255,255,255,0.8);
}
.back{
	position: absolute;
    left: 6px;
    top: 0;
    color:#FFCD32;
    height: 40px;
    width: 40px;
    line-height: 40px;
    font-size: 26px;
    z-index: 50;
}
.singer-name{
	width: 100%;height: 40px;font-size: 18px;line-height: 40px;font-weight: 500;z-index: 40;
}
.blurBg{
	position: fixed;
	top: 0;left: 0;right: 0;bottom: 0;
	z-index: -1;
	opacity: .6;
	filter: blur(20px);
}
.content{position: relative;width: 100%;}
.content-l{position: relative;width: 100%;height: 100%;}
.song-img{width: 80%;padding-bottom: 80%;height: 0;margin: 0 auto;position: relative;}
.song-img-1{width: 100%;height: 100%;border:10px solid rgba(255,255,255,0.1);position: absolute;top: 0;left: 0;border-radius: 50%;overflow: hidden;}
.song-desc{margin: 30px auto 0 auto;height: 20px;line-height: 20px;font-size: 14px;color: rgba(255,255,255,0.5);}
.song-lycing p{line-height: 32px;
    color: rgba(255,255,255,0.5);
    font-size: 14px;overflow: hidden;height: 32px;padding: 0 5px;}

.content-r{
	position: relative;overflow: hidden;
}
.singerName{
	font-size: 14px;font-family: "黑体";font-weight: normal;
}
.miniscreen{
	position: fixed;
	left: 0;right: 0;bottom: 0;height: 60px;
	z-index: 180;
	background: #333;
}
/*全屏动画*/
.fullscreen-enter-active,.fullscreen-leave-active{
	transition: all 1s
}
.fullscreen-enter,.fullscreen-leave-to{
	opacity: 0;
}
.fullscreen-enter-active .top,.fullscreen-leave-active .top{
	transition: all .5s cubic-bezier(.8,.1,.8,1.84)
}
.fullscreen-enter .top,.fullscreen-leave-to .top{
	transform: translate3d(0, -40px, 0)
}

.fullscreen-enter-active .play-list,.fullscreen-leave-active .play-list{
	transition: all .5s cubic-bezier(.8,.1,.8,1.84)
}
.fullscreen-enter .play-list,.fullscreen-leave-to .play-list{
	transform: translate3d(0, 8 5px, 0)
}


/*mini迷你动画*/
.mini-enter-active,.mini-leave-active{
	transition: all 1s cubic-bezier(.8,.1,.8,1.84);
}
.mini-enter,.mini-leave-to{
	opacity: 0;
}
.content{
	position: fixed;top:80px;bottom: 170px;width: 100%;
}
.bottom{
	position: absolute;bottom: 50px;width: 100%;
}
</style>



