<!--创建scroll组件-->
<template>
  <div ref="wrapper">
   		<slot></slot>
  </div>
</template>

<script>
	import BScroll from 'better-scroll'
	export default {
			components:{
				BScroll
			},
		//接收参数,必须动态加载才能激发上下滑动效果
			props:{
				probeType:{
					type:Number,
					default:1
				},
				click:{
					type:Boolean,
					default:true
				},
				//接收datas,检测变化，就刷新高度
				datas:{
					type:Array,
					default:null
				},
//				是否监听滚动
				listenScroll:{
					type:Boolean,
					default:false
				},
				//是否监听上拉加载
				scrollUp:{
					type:Boolean,
					default:false
				}
			},
			mounted(){
				//页面一加载，过20毫秒初始化
				this.$nextTick(function(){
						setTimeout(()=>{
							this.initBScroll();
						},20)
				})
			},
			methods:{
				//初始化
				initBScroll(){
					//如果dom节点不存在，返回
					if(!this.$refs.wrapper){
						return;
					}
					this.scroll=new BScroll(this.$refs.wrapper,{
						probeType:this.probeType,
						click:this.click
					})
					//如果监听了listenScroll滚动事件；
					if(this.listenScroll){
						let thats=this
						this.scroll.on('scroll',function(pos){
							thats.$emit('scrollFun',pos)
						})
					}
					//如果开启了上拉加载
					if(this.scrollUp){
						let that=this
						this.scroll.on('scrollEnd',()=>{
							let y=this.scroll.y
							console.log(y)
							let maxScrollY=this.scroll.maxScrollY
							//表示拉到底部了
							if(y <= (maxScrollY +100)){
								//派发一个方法出去
								this.$emit('scrollToEnd')
							}

						})
					}
				},
				//刷新
				refresh(){
					this.scroll && this.scroll.refresh()
				},
				gotoElement(){
					//增加apply方法，修改this的指向
					this.scroll && this.scroll.scrollToElement.apply(this.scroll,arguments)
				}
			},
			//检测data数据变化,变化就刷新
			watch:{
				datas(){
					setTimeout(()=>{
						//刷新
						this.refresh()
					},20)
				}
			}
	}
</script>



