<!--创建scroll组件-->
<template>
	<Scroll class="listview" :data='listViews' ref='scroll' :listenScroll='listenScroll' @scrollFun="scrollFun" :probeType='probeType'>
		<ul>
	      <li v-for="items in listViews" class="list-group" ref="listElement">
	        <h2 class="list-group-title">{{items.title}}</h2>
	        <uL>
	          <li v-for="item in items.item" class="list-group-item" @click="emitClick(item)">
	            <img class="avatar" v-lazy="item.Fsinger_img" @load="loadImg">
	            <span class="name">{{item.Fsinger_name}}</span>
	          </li>
	        </uL>
	      </li>
	    </ul>
	    <div class="list-shortcut" @touchstart='ontouch'>
			<ul>
				<li v-for="(shot,k) in shotList" :data-index='k' class="item" :class="{'current':currentIndex==k}">
					{{shot}}
				</li>
			</ul>
	    </div>
	    <div class="list-fixed" v-show="listTitle">
	    	<div class="fixed-title">{{listTitle}}</div>
	    </div>
	</Scroll>
</template>--`

<script>
	import Scroll from 'base/scroll/scroll.vue';

	export default {
		//接收参数
			data(){
				return {
					scrollY:-1,
					currentIndex:0
				}
			},
			props:['listViews'],
			components:{
				Scroll
			},
			created(){
				this.probeType=3,
				this.listenScroll=true,
				this.listHeight=[]
			},
//			当数据接收到,监听到数据变化后,计算高度
			watch:{
				listViews(){
					setTimeout(()=>{
						this._jisuanHeight()
					},20)
				},
//				检测scrollY的值,和每个list高度对比,看在哪个区间
				scrollY(newY){
					//保存listHeight的值
					if(newY>0){
						this.currentIndex=0;
						return;
					}
					const listsheights=this.listHeight;
					for(let i=0;i<listsheights.length;i++){
						let h1=listsheights[i]
						let h2=listsheights[ i + 1]
						if(-newY >= h1 && -newY < h2){
							this.currentIndex=i
							return
						}
					}
					this.currentIndex=listsheights.length-2
				}
			},
			methods:{
				emitClick(item){
					this.$emit('getEmitClick',item)
				},
				loadImg(){
					this.$refs.scroll.refresh()
				},
				ontouch(e){
					let index=e.target.getAttribute('data-index')
					if(!index){return}
					this.$refs.scroll.gotoElement(this.$refs.listElement[index],1000)
				},
				scrollFun(pos){
					this.scrollY=pos.y;
				},
//				计算高度
				_jisuanHeight(){
					this.listHeight=[]
					const list=this.$refs.listElement;
					let height=0;
					this.listHeight.push(height)
					for(let i=0;i<list.length;i++){
						let h1=list[i];
						height+=h1.clientHeight;
						this.listHeight.push(height)
					}
					console.log(this.listHeight)
				}
			},
			computed:{
				shotList(){
					var shot=[]
					for(var i in this.listViews){
						let val=this.listViews[i].title
						shot.push(val.substr(0,1))
					}
					return shot
				},
				listTitle(){
					if(this.scrollY>0){
						return ''
					}
					return this.listViews[this.currentIndex]?this.listViews[this.currentIndex].title:''
				}
			}
	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">

	 .listview{
		position:relative;
    	width:100%;
    	height: 100%;
    	overflow: hidden;
	 }

    .listview .list-group{padding-bottom: 30px}
      .listview .list-group .list-group-title{
      	height:30px;
        line-height:30px;
        padding-left:20px;
        text-align: left;
        font-size: 12px;
        background: #333;
        color: rgba(255,255,255,0.5);
      }

      .listview .list-group .list-group-item{
      	display: flex;
        align-items: center;
        padding: 20px 0 0 30px;
      }

       .listview .list-group .avatar{
        	width:50px;
          height:50px;
          border-radius: 50%;
        }

        .listview .list-group .name{
        	margin-left: 20px;
          font-size: 14px;
        }

    .listview .list-shortcut{
      position: absolute;
      z-index: 30;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      width: 20px;
      padding: 20px 0;
      border-radius: 10px;
      text-align: center;
      font-family: Helvetica;
    }

     .listview .list-shortcut .item{
      	padding: 3px;
      	color: rgba(255,255,255,0.5);
      	font-size: 13px;
        line-height:1;
      }
      .list-shortcut .item.current{
          color:red;
          }
   .list-fixed{
    	  position: absolute;
	      top: 0;
	      left: 0;
	      width: 100%;
    }

      .fixed-title{
      	height: 30px;
	    line-height: 30px;
	    padding-left: 20px;
	    font-size: 12px;
	    color: rgba(255,255,255,0.5);
	    background: #333;
	    text-align: left;
      }

    .loading-container{
    	position: absolute;
      width: 100%;
      top: 50%;
      transform: translateY(-50%);
    }

</style>



