import {setSearch} from 'api/catch.js'
const playMode={
		'shunxu':0,
		'shuiji':1,
		'danqu':2
}
const state={
	singer:{},
	playing:false,
	currentIndex:-1,
	fullScreen:false,
	playList:[],
	shunxuList:[],
	modes:playMode.shunxu,
//	取localstoratage里面的值
	searchHistory:setSearch()
}

export default state


