<template>
  <div class="search">
    		<SearchBox :placeHolder='placeHolder' @searchs='searchs' ref="seaBox"></SearchBox>
    		<div class="hot" v-show="!searchText">
    			<h2>热门搜索</h2>
    			<ul class="hotUl">
    				<li v-for="item in HotData" @click="addQuery(item.k)">{{item.k}}</li>
    			</ul>
    		</div>
    		<div class="history" v-show="mapsearchHistory.length >0 && !searchText">
    			<div class="historyWrap">
    				<h2>搜索历史</h2>
    				<i class="fa fa-trash" aria-hidden="true" @click="clearAlls()"></i>
    			</div>
    			<ul class="history-ul">
    				<li v-for="item in mapsearchHistory" @click="clickSearch(item)">
    					<p>{{item}}</p>
    					<i class="fa fa-times" aria-hidden="true" @click.stop=clearSearch(item)></i>
    				</li>
    			</ul>
    		</div>
    		<Confirm ref='confirm' @selectDelelte='clearAll()' title="是否删除全部记录" yesBtn="删除"></Confirm>
    		<div class="searchDetail" v-show="searchText">
    			<SearchDetail :searchText="searchText" :page="page" :zhida="zhida" @setHistory='setHistory'></SearchDetail>
    		</div>
				<!--添加一个搜索页面的二级歌手详情页面-->
				<router-view></router-view>
  </div>
</template>

<script>
	import SearchBox from 'components/search-box/searchBox.vue'
	import SearchDetail from 'components/search-detail/searchDetail.vue'
	import Confirm from 'base/confirm/confirm.vue'
	import {seaechHot} from 'api/search.js'
	import {mapActions,mapGetters,mapMutations} from 'vuex'
	export default {
				components:{
					SearchBox,
					SearchDetail,
					Confirm
				},
				computed:{
					...mapGetters([
						'mapsearchHistory'
					])
				},
				data(){
					return{
						placeHolder:'搜索歌曲，歌手',
						HotData:'',
						searchText:'',
						page:1,
						zhida:true
					}
				},
				methods:{
					searchs(text){
							this.searchText=text
					},
					clickSearch(text){
						this.$refs.seaBox.setQuery(text);
					},
					clearSearch(text){
						console.log('我要删除'+text)
						this.clearOne(text)
					},
					clearAlls(){
						this.$refs.confirm.show()
//						this.clearAll()
					},
					setHistory(item){
						let newHistory=item.songname
						this.saveSearch(newHistory)
					},
					addQuery(k){
						//把值传到子组件，顺便调用子组件的方法
						this.$refs.seaBox.setQuery(k);
					},
					_HotSearch(){
						seaechHot().then((msg)=>{
							console.log(msg.data.hotkey)
							this.HotData=msg.data.hotkey.slice(0,10)
						})
					},
					...mapActions([
						'selectPlay',
						'saveSearch',
						'clearOne',
						'clearAll'
					]),
					...mapMutations({
						'setSearchHistory':'setSearchHistory'
					})
				},
				created(){
					this._HotSearch();
					console.log(this.mapsearchHistory)
				}

	}
</script>

<style scoped lang="stylus" rel='stylesheet/stylus'>
.hot,.history{padding: 0 15px;}
h2{
	font-size: 14px;color: rgba(255,255,255,0.5);text-align:left
}
.hotUl{position: relative;overflow: hidden;text-align: left;margin-top: 20px;}
.hotUl li{display: inline-block;padding: 5px 8px;background: #333;border-radius: 5px;color: rgba(255,255,255,0.5);font-size: 14px;margin: 0 10px 10px 0;}
.historyWrap{display: flex;padding: 20px 0;}
.historyWrap h2{flex:1;}
.history-ul li{display: flex;padding: 8px 0;}
.history-ul li:active{background: rgba(255,255,255,.2);color: #fff;}
.history-ul li p{flex:1;text-align: left;margin: 0;padding: 0;}
.history .fa{color: rgba(255,255,255,.5);}
.searchDetail{
	position: fixed;
	top: 150px;bottom: 0;width: 100%;
}
</style>



