<template>
	<div class="song-list">
		<ul>
			<li class="item-list" v-for="(item,index) in songsdata" @click="selectItem(item,index)">
				<div class="item-icon">
					<span></span>
				</div>
				<div class="item-text">
					<h2>{{item.name}}</h2>
					<p>{{item.singer}} - {{item.album}}</p>
					<a href=""></a>
				</div>
			</li>
		</ul>
	</div>
</template>
<script>
	export default {
		props:{
			songsdata:{
				type:Array,
				default:function(){
					return []
				}
			},
			title:{
				type:String,
				default:''
			}
		},
		methods:{
			//这是基础组件,不写逻辑,只派发一个方法出去
			selectItem(item,index){
				this.$emit('select',item,index)
			}
		}
	}
</script>
<style rel='stylesheet/stylus' lang="stylus" scoped="scoped">
.song-list{
	padding: 20px 30px;
}
.item-list{display: flex;align-items: center;height: 64px;}
.item-icon{flex:0 25px; height: 26px;background: yellow;margin-right: 26px;}
.item-text{flex:1;}
.item-text{text-align: left;}
.item-text h2{font-size: 14px;color: #fff;}
</style>


