<template>
	<div class="confirm" v-show="showConfirm">
		<div class="content">
			<p class="confirm-text">{{title}}</p>
			<div class="btns">
				<a class="btn left" @click="hide">{{noBtn}}</a>
				<a class="btn" @click="deleteAll">{{yesBtn}}</a>
			</div>
		</div>
	</div>
</template>
<script>
	export default{
		props:{
			title:{
				type:String,
				default:''
			},
			yesBtn:{
				type:String,
				default:'是'
			},
			noBtn:{
				type:String,
				default:'取消'
			}
		},
		data(){
			return{
				showConfirm:false
			}
		},
		methods:{
			show(){
				this.showConfirm=true
			},
			hide(){
				this.showConfirm=false
			},
			deleteAll(){
				this.hide()
				this.$emit('selectDelelte')
			}
		}
	}
</script>
<style rel="stylesheet/stylus" lang="stylus" scoped="scoped">
	.confirm{
		position: fixed;
		left: 0;right: 0;top: 0;bottom: 0;
		 text-align: center;
		 background: rgba(0,0,0,.5);
	}
	.content{
		position: absolute;
		width: 270px;
		left: 50%;
		top: 50%;
		transform: translate(-50%,-50%);
		background: #333;
		border-radius: 10px;
	}

    .confirm-text{
	    font-size: 18px;line-height: 20px;
	    margin: 0;padding: 15px;
	    color:rgba(255,255,255,.5)
    }
    .btns{display: flex;border-top: 1px solid #242424;}
    .btn{flex:1;padding: 15px;color:rgba(255,255,255,.3)}
    .btn.left{border-right: 1px solid #242424;}

</style>


