<template>
	<div class="recommend">
	  <Scroll class="recommend-content" :datas="songList" ref="RefScroll">
	  	<div class="allwraper">
			<div class="rec-swiper" v-if="sliders.length">
				<mt-swipe :auto="2000">
					<mt-swipe-item v-for="(item,k) in sliders" :key='k'>
						<!--监听图片一加载就刷新scroll-->
						<a :href="item.linkUrl">
							<img class="needsclick" :src="item.picUrl" @load="loadImg"/>
						</a>
					</mt-swipe-item>
				</mt-swipe>
			</div>
			<div class="hot-gedan">
				<h3>热门歌单推荐</h3>
				<ul class="mui-table-view">
					<li class="mui-table-view-cell mui-media" v-for="item in songList">
						<a href="www.baidu.com" class="">
							<!--<img class="mui-media-object mui-pull-left" :src="item.picUrl" @load="loadImg">-->
							<img class="mui-media-object mui-pull-left" v-lazy="item.picUrl" @load="loadImg">
							<div class="mui-media-body">
								{{item.pic_mid}}
								<p class="mui-ellipsis">{{item.songListDesc}}</p>
							</div>
						</a>
					</li>
				</ul>
			</div>
	   </div>
	   <!--当数据没有的时候就显示-->
	   <div class="loadWraper" v-show="!songList.length>0">
	   		<loading></loading>
	   </div>

      </Scroll>
	</div>
</template>

<script>
	import { Swipe, SwipeItem } from 'mint-ui'
	import 'mint-ui/lib/style.css'
	import Scroll from 'base/scroll/scroll'
	import {getRecommends} from 'api/recommend.js'
	import Loading from 'base/loading/loading.vue'
//	import json2 from 'api/json.js'
	export default {
		data(){
			return {
				sliders:[],
				songList:[]
			}
		},
		components: {
			'mt-swipe': Swipe,
			'mt-swipe-item': SwipeItem,
			'Scroll':Scroll,
			'Loading':Loading
		}
		,
		created(){
			this._getRecommend();
		},
		methods:{
			_getRecommend(){
				let that=this;
				getRecommends().then((res) => {
					that.sliders=res.data.slider
					that.songList=res.data.songList
			        })
			},
			//				图片加载了右重新刷新
			loadImg(){
				if(!this.loadImage){
					this.$refs.RefScroll.refresh();
					this.loadImage=true;
				}
			}
		}
	}
</script>

<style scoped lang="stylus" rel='stylesheet/stylus'>
@import "~assets/css/a.styl";

	.recommend{
	    position: fixed;
	    top: 80px;
	    width: 100%;
	    bottom: 0;
	}
	.recommend-content{
		height: 100%;overflow: hidden;
	}
	.rec-swiper {
		height: 15rem;
	}
	.mint-swipe-item img {
		width: 100%;
		height: 100%;
	}
	.hot-gedan h3{
		color: #FFCD32;
		padding: 30px 0;
		font-size: 1.4rem;
	}
	.mui-table-view,.mui-table-view-cell:after,.mui-table-view-cell:after,.mui-table-view:before,.mui-table-view:after{
		background: transparent;
	}
	.mui-media-body{text-align: left;color: #fff;font-size: 1.4rem;line-height: 2.4rem;}
	.mui-ellipsis{color: rgba(255,255,255,0.3);}
	.loadWraper{
		position: absolute;
		width: 100%;
		top: 50%;
		left: 0;
	}
</style>


