<template>
  <div class="singer">
  																<!--接收派发的方法，不需要带参数，直接方法名 如果是go(item),那么就：：“go”-->
  	<listView :listViews=singers @getEmitClick="EmitClicked"></listView>
  	<router-view></router-view>
  	<div class="loadWraper" v-show="!singers.length">
  		<Loading></Loading>
  	</div>
  </div>
</template>

<script>
	import {getSinger} from 'api/singer.js'
	import listView from 'base/listView/listView.vue'
	import Loading from 'base/loading/loading.vue'
	//想用mutations里面的方法，先引mapMutations ,在methods中...mapMutations({})注册
	import {mapMutations} from 'vuex'
	export default {
		data(){
			return {
				singers:{}
			}
		},
		components:{
			listView,
			Loading
		},
			created(){
				this._getSingers()
			},
			methods:{
				//1，组册mutations里面的方法
				...mapMutations({
					setSingers:'setSinger'
				}),
				EmitClicked(item){
					this.$router.push({path:`/singer/${item.Fsinger_mid}`})
					//2，调用mutations里面的方法
					this.setSingers(item)
				},
				//获取歌手列表
				_getSingers(){
					var that=this;
					getSinger().then((msg)=>{
						that.singers=this._DiyData(msg.data.list)
					})
				},
				_DiyData(lists){
					console.log(lists)
					//创建一个map对象，
					let map={
						Hot:{
							title:'热门',
							item:[]
						}
					}
					let Hot=[]
					lists.forEach((k,i)=>{
						if(i<10){
							map.Hot.item.push({
								Fsinger_mid:k.Fsinger_mid,
								Fsinger_name:k.Fsinger_name,
								Fsinger_img:'https://y.gtimg.cn/music/photo_new/T001R300x300M000'+k.Fsinger_mid+'.jpg?max_age=2592000'
							})
						}
						let key=k.Findex;
						//先创建key对象，才能赋值
						if(!map[key]){
							map[key]={
								title:key,
								item:[]
							}
						}
						//再赋值，才能赋值
						map[key].item.push({
								Fsinger_mid:k.Fsinger_mid,
								Fsinger_name:k.Fsinger_name,
								Fsinger_img:'https://y.gtimg.cn/music/photo_new/T001R300x300M000'+k.Fsinger_mid+'.jpg?max_age=2592000'
						})
					})
					//再次处理map数组，变成想要的样子
					let hot=[];
					let diyArray=[];
					for(var i in map){
						let val=map[i]
						if(val.title.match(/[a-zA-Z]/)){
							diyArray.push(val)
						}else if(val.title === '热门'){
							hot.push(val)
						}
					}
					//sort排序规则，
					function sor(a,b){
						return a.title.charCodeAt(0)-b.title.charCodeAt(0)
					}
					diyArray.sort(sor)
					console.log(hot.concat(diyArray))
					return hot.concat(diyArray)
				}
			}
	}
</script>

<style scoped lang="stylus" rel='stylesheet/stylus'>
.singer{color: #fff;
position: fixed;
    top: 88px;
    bottom: 0;
    width: 100%;
}
.loadWraper{
	position: absolute;
	width: 100%;
	top: 50%;
}
</style>



