import json2 from 'api/json.js'

//查询热搜
export function seaechHot(){
	let url="https://c.y.qq.com/splcloud/fcgi-bin/gethotkey.fcg"
	let data=[
		{g_tk: 590801272},
		{uin: 351330743},
		{format: 'json'},
		{inCharset: 'utf-8'},
		{outCharset: 'utf-8'},
		{notice: 0},
		{platform: 'h5'},
		{needNewCode: 1}
	]

	return json2(url,data)
}

//搜索歌曲和歌手
export function searchSong(name,page,zhida){
	let url="https://c.y.qq.com/soso/fcgi-bin/search_for_qq_cp"
	let data=[
		{w: name},
		{p:page},
		{catZhida: zhida ? 1 :0},
		{zhidaqu: 1},
		{t: 0},
		{flag: 1},
		{ie: 'utf-8'},
		{sem: 1},
		{aggr: 0},
		{perpage: 20},
		{n: 20},
		{remoteplace: 'txt.mqq.all'},
		{uin: 0},
		{needNewCode: 1},
		{platform: 'h5'}
	]
	return json2(url,data)
}



