//获取方法
export function mapSingerFun(state){
	return state.singer
}

export const mapPlaying = state=>state.playing

export const mapCurrentIndex = state=>state.currentIndex

export const mapCurrentSong = (state)=>{
	return state.playList[state.currentIndex] || {}
}

export const mapFullScreen = state=>state.fullScreen

export const mapPlayList = state=>state.playList

export const mapShunxuList =state=>state.shunxuList

export const mapMode =state=>state.modes

export const mapsearchHistory= state =>state.searchHistory


