import json2 from 'api/json.js'

//获取歌手列表
export function getSinger(){
	var url ='https://c.y.qq.com/v8/fcg-bin/v8.fcg'
	var data=[
		{channel:'singer'},
		{page:'list'},
		{key:'all_all_all'},
		{pagesize:100},
		{pagenum:1},
		{g_tk:1174192068},
		{loginUin:0},
		{hostUin:0},
		{format:'jsonp'},
		{inCharset:'utf8'},
		{outCharset:'utf-8'},
		{notice:0},
		{platform:'yqq'},
		{needNewCode:0}
	];
	return json2(url,data)
}
//获取歌手的歌曲列表
export function getSingerList(mid,num){
	let url='https://c.y.qq.com/v8/fcg-bin/fcg_v8_singer_track_cp.fcg'
	let data=[
		{g_tk:1174192068},
		{loginUin:0},
		{hostUin:0},
		{format:'jsonp'},
		{inCharset:'utf8'},
		{outCharset:'utf-8'},
		{notice:0},
		{platform:'yqq'},
		{needNewCode:0},
		{singermid:mid},
		{order:'listen'},
		{begin:0},
		{num:num},
		{songstatus:1}
	]

	return json2(url,data)
}



