<template>
  <div class="tab">
  	<mt-navbar class='tab-nav'>
		  <mt-tab-item id="1">
		  	<router-link to='/recommend' tag='div'>推荐</router-link>
		  </mt-tab-item>
		  <mt-tab-item id="2">
		  	<router-link to='/singer' tag='div'>歌手</router-link>
		  </mt-tab-item>
		 <mt-tab-item id="2">
		  	<router-link to='/rank' tag='div'>排行</router-link>
		  </mt-tab-item>
		  <mt-tab-item id="2">
		  	<router-link to='/search' tag='div'>搜索</router-link>
		  </mt-tab-item>
		</mt-navbar>

  </div>
</template>

<script>
	import {Navbar, TabItem} from 'mint-ui'
	import 'mint-ui/lib/style.css'
	export default {
	  components:{
	  	'mt-navbar':Navbar,
	  	'mt-tab-item':TabItem
	  }
	}
</script>

<style scoped lang="stylus" rel='stylesheet/stylus'>
@import '~assets/stylus/base.styl';
.tab-nav{
	background:$bg-color;
}
.tab-nav a{
	padding: 5px 0;
	color: rgba(255,255,255,0.5);
}
.tab-nav .mint-tab-item-label>div{
	display: inline-block;
    padding-bottom: 10px;
    font-size: 1.4rem
}
.tab-nav a .router-link-active{
	color: #ffcd32;
  border-bottom: 2px solid #ffcd32;
}
</style>



