<template>
  <Scroll class="searchDetail2" :datas='songsData' :scrollUp="scrollUp" @scrollToEnd="scrollToEnd" ref="scrollE">
			<ul>
				<li v-for="item in songsData" @click="goMusic(item)">
					<div class="detail-icon">
						<i class="fa" aria-hidden="true" :class="setIcon(item)"></i>
					</div>
					<div class="detail-name">
						<p v-html="setName(item)"></p>
					</div>
				</li>
				<Loading :title="upTitle" v-show="hasMore"></Loading>
			</ul>
			<NoreSult v-show="!hasMore && !songsData.length" class="no-result-warp" title='对不起，暂未搜索到...'></NoreSult>
  </Scroll>
</template>

<script>
	import {searchSong} from "api/search.js"
	import Scroll from "base/scroll/scroll.vue"
	import Loading from "base/loading/loading.vue"
	import {mapMutations,mapActions} from 'vuex'
	import NoreSult from 'base/no-result/no-result.vue'
	import {createNewSongsList} from 'assets/js/songlist.js'
	export default {
			data(){
				return{
					songsData:[],
					page:1,
					//上拉加载
					scrollUp:true,
					upTitle:'',
					hasMore:true
				}
			},
			components:{
				Scroll,
				Loading,
				NoreSult
			},
			props:{
					searchText:{
						type:String,
						default:''
					},
					zhida:{
						type:Boolean,
						default:true
					}
			},
			methods:{
					_SearchSongs(name){
						this.page=1
						this.$refs.scrollE.gotoElement(0,0)
						this.hasMore=true

						if(typeof(name) !=='undefind'){
							searchSong(name,this.page,this.zhida).then((msg)=>{
								if(msg.code == 0){
									//整理数据
									this.songsData=this._resetData(msg.data)
									//检测是否还有更多数据
									this._checkHasMore(msg.data)
								}
								console.log(msg)
							})
						}
					},
					_resetData(data){
						let newData=[]
						if(data.zhida && data.zhida.singerid){
							data.zhida['type']='singer'
							newData.push(data.zhida)
						}
							console.log(newData)
						if(data.song){
							newData=newData.concat(data.song.list)
						}
						console.log(newData)
						return newData
					},
					setIcon(item){
						if(item.type =='singer'){
							return 'fa-user'
						}else{
							return 'fa-music'
						}
					},
					setName(item){
						if(item.singername){
							return item.singername
						}else{
							return item.songname + ' - '+this.resetSinger(item.singer)
						}
					},
					//拼姐歌手字符串
					resetSinger(singers){
						let singer=[]
						for(let i=0;i<singers.length;i++){
							singer.push(singers[i].name)
						}
						return singer.join(' / ')
					},
					//接收滚动插件，滚到底部，上拉加载的方法
					scrollToEnd(){
						if(!this.hasMore){
							return
						}
						this.page++
						searchSong(this.searchText,this.page,this.zhihda).then(msg=>{
							//把原来的值和现在的值concat合并
							this.songsData = this.songsData.concat(this._resetData(msg.data))
							//继续检测是否还有更多数据
							this._checkHasMore(msg.data)
						})
						console.log('我滚动到底部了')
					},
					_checkHasMore(data){
						let songs=data.song
						let totalnum=songs.totalnum
						let totalPage=Math.floor(totalnum/20)
						if(!songs.list.length || this.page > totalPage){
							this.hasMore=false
						}
					},
					goMusic(item){
							let items={}
						if(item.type == 'singer'){
							console.log('我跳歌手')
							//组合一个新数据，改变vuex里面的歌手信息
							items.Fsinger_mid=item.singermid
							items.Fsinger_name=item.singername
							//把当前歌曲信息传入vuex
							this.setSinger(items)
							this.$router.push({
								path:'/search/'+item.singermid
							})
						}else{
//							item.singer=item.singer[0].name
//							item.name=item.songname
//							item.duration=item.interval
								items=new createNewSongsList(item)
								this.insertSong(items)
								console.log(items)
						}
						this.$emit('setHistory',item)
					},
					...mapMutations({
						'setSinger':'setSinger'
					}),
					...mapActions([
						'insertSong'
					])
			},
			watch:{
				searchText(newText){
					console.log(newText,this.page,this.zhida)
					this._SearchSongs(newText)
				}
			}
	}
</script>

<style scoped lang="stylus" rel='stylesheet/stylus'>
@import '~assets/stylus/base.styl';
	.searchDetail2{
		padding:20px 15px;
		color: #fff;
		height: 100%;
		overflow: hidden;
	}
	.searchDetail2 ul{
		padding-bottom: 20px;
	}
	.searchDetail2 ul li{display: flex;align-items: center;padding-bottom: 20px;}
	.searchDetail2 ul li .detail-icon{flex: 0 0 30px;width: 30px;margin-right: 10px;}
	.searchDetail2 ul li .detail-name{flex:1;text-align: left;}
	.searchDetail2 ul li .detail-name p{margin: 0;padding: 0;}
	.loading-img{
			border: 1px solid red;height: 40px;width: 100%;
	}
	.loading-img img{
			width:24px;height: 24px ;
	}
	.no-result-warp{
		position: absolute;
      width: 100%;
      left: 0;
      top: 50%;
      margin-top:-75px;
     }
</style>



