//暴露一个ajax的公共方法
export default function json2(url,data){
//	url += (url.indexOf('?') < 0 ? '?' : '&') + param(data);
var data=(url.indexOf('?') < 0 ? '' : '&') + param(data);

	return new Promise(function(resolve,reject){
		$.ajax({
			type:'get',
			url:url,
			async:true,
			dataType:'JSONP',
			data:data,
			jsonp: "jsonpCallback",
			crossDomain: true == !(document.all),
			success:function(msg){
				console.log('查询成功');
				resolve(msg);
			},
			error:function(e){
				reject(e)
			}
		})
	});
}

//暴露一个整理参数的方法
export function param(data) {
  let url = ''
  for (var k in data) {
    let value = data[k] !== undefined ? data[k] : ''
    for(var i in value){
    	 url += '&' + i + '=' + encodeURIComponent(value[i])
    }

  }
  return url ? url.substring(1) : ''
}



