<template>
  <div class="rank">
  	<Scroll ref="wrapper" class='listView'>
		    <ul>
		    	<li v-for="item in datas">
		    		<div class="li-img">
		    			<img v-lazy="item.picUrl"/>
		    		</div>
		    		<div class="li-right">
		    			<p v-for="(song,k) in item.songList">{{k+1}} . {{song.songname}} - {{song.singername}}</p>
		    		</div>
		    	</li>
		    </ul>
  	</Scroll>
  	<div class="loadWraper" v-show="!datas.length">
  		<Loading></Loading>
  	</div>
  </div>
</template>

<script>
	import Scroll from 'base/scroll/scroll.vue'
	import {getRank} from 'api/rank.js'
	import Loading from 'base/loading/loading.vue'
	export default {
			components:{
				Scroll,
				Loading
			},
			data(){
				return{
					datas:[]
				}
			},
			created(){
				this.getRanks();
			},
			methods:{
				getRanks(){
					getRank().then((msg)=>{
						this.datas=msg.data.topList;
						console.log(this.datas)
					})
				}
			},
			watch:{
				datas(){
					setTimeout(()=>{
						this.$refs.wrapper.refresh()
					},20)

				}
			}
	}
</script>

<style scoped lang="stylus" rel='stylesheet/stylus'>
.rank{
	position: fixed;
	width: 100%;top: 88px;bottom: 0;
}
.listView{height: 100%;overflow: hidden;}
.rank ul{
	overflow:hidden;
	padding: 0 15px 20px;
}
.rank ul li{
	position: relative;
	padding-left: 100px;
	padding-top: 20px;
}
.rank ul li .li-img{
	position: absolute;top: 20px;left: 0;
	width: 100px;height: 100px;display: inline-block;
}
.rank ul li .li-img img{
	width: 100%;height: 100%;
}
.rank ul li .li-right{
	width: 100%;height: 100px;padding: 10px 0 10px 15px;
	    background-color: rgb(51, 51, 51)
}
.rank ul li .li-right p{
	text-align: left;
	color: rgba(255,255,255,0.3);
	font-size: 13px;
	margin: 0;line-height: 28px;
	overflow: hidden;text-overflow: ellipsis;white-space: nowrap;
}
.loadWraper{
	width: 100%;
	position: absolute;
	top: 50%;
}
</style>



