//引入刚刚的json.js的公告方法 jsonp方法
import getRecommend from 'api/json.js'

//暴露一个获取推荐信息的方法
export function getRecommends(){
	var url='https://c.y.qq.com/musichall/fcgi-bin/fcg_yqqhomepagerecommend.fcg';
	var data=[
		  {g_tk: 5381},
		  {inCharset: 'utf-8'},
		  {outCharset: 'utf-8'},
		  {notice: 0},
		  {format: 'json'},
//		  {param: 'jsonpCallback'},
		  {platform: 'h5'},
	      {uin: 0},
	      {needNewCode: 1},
	      {hostUin:0}
	]
	return getRecommend(url,data)
}


